
















	export default {
		data() {
			return {
				submitLists:[],
				orderInfo:{
					account:'',
					orderStatus:'1'
				}
			};
		},
		onLoad() {
			var that = this;
			this.orderInfo.account = uni.getStorageSync('userInfo').account;
			const jsonString = {
				orderInfo:this.orderInfo,
				requestType:"getOrdeFromAccount",
			}
			const param ={
				controllerRequestType:"orderControllerService",
				jsonString:JSON.stringify(jsonString)
			}
			uni.request({
				url:this.websiteUrl,
				method:'POST',
				header:{
					"content-type":"application/x-www-form-urlencoded"
				},
				data:param,
				success(res) {
					if(res.data.errorCode == '0000'){
						that.submitLists = res.data.orderInfo.ordeList;		
					 }else{
// 						uni.showToast({
// 							icon:'none',
// 							title:res.data.errorMessage
// 						})
					}
				},
				fail(res) {
					uni.showToast({
						icon:'none',
						title:'网络异常，请稍后重试'
					})
				}
			})
		},
		methods:{
			goSubmitContent(task){
				uni.navigateTo({
					url:'./submitContent?task='+JSON.stringify(task)
				})	
				
			}
		}
	}



